import React, { useEffect, useState } from 'react';
import './App.scss';

export const App = () => {
  const [color, setColor] = useState('');
  const [answers, setAnswers] = useState([]);
  const [isCorrect, setIsCorrect] = useState(false);

  function renderColors() {
    const colorGenerator = () => {
      const randomColor = Math.floor(Math.random() * 16777215).toString(16);

      return `#${randomColor}`;
    };

    const actualColor = colorGenerator();

    setColor(actualColor);
    setAnswers([actualColor, colorGenerator(), colorGenerator()]
      .sort(() => 0.5 - Math.random()));
  }

  const handlerOnClick = (property) => {
    if (property === color) {
      setIsCorrect(true);
      renderColors();

      return;
    }

    setIsCorrect(false);
  };

  useEffect(() => {
    renderColors();
  }, []);

  return (
    <div className="App">
      <div className="box" style={{ background: color }} />

      {answers.map(answer => (
        <button
          className="box__button"
          key={answer}
          onClick={() => {
            handlerOnClick(answer);
          }}
          type="button"
        >
          {answer}
        </button>
      ))}

      {isCorrect && (
      <p style={{ color: 'green' }}> Correct answer</p>
      )}

      {!isCorrect && (
      <p style={{ color: 'red' }}> Wrong answer</p>
      )}
    </div>
  );
};
